import React, { useEffect, useState } from 'react';
import styles from './PaymentInfo.module.scss';
import classNames from 'classnames/bind';

const cx = classNames.bind(styles);

function TotalPaymentInfo({ props }) {
  const [data, setData] = useState({});

  useEffect(() => {
    let totalRoom = 0;
let totalElectric = 0;
let totalWater = 0;
let totalElectricUsed = 0;
let totalWaterUsed = 0;
let roomCount = [];

const filteredRooms = props.filter((item) => item.GiaPhong > 0);

filteredRooms.forEach((item) => {
  const electricUsed = item.chisodienmoi - item.chisodiencu;
  const waterUsed = (item.chisonuoclanhmoi + item.chisonuocnongmoi) - 
                    (item.chisonuoclanhcu + item.chisonuocnongcu);

  const hasElectric = electricUsed > 0;
  const hasWater = waterUsed > 0;

  // Ch tnh phng nu c in hoc nc tiu th
  if (hasElectric || hasWater) {
    totalRoom += item.GiaPhong;
    roomCount.push(item.SoPhong);

    if (hasElectric) {
      totalElectric += electricUsed;
      totalElectricUsed += electricUsed * 4000;
    }

    if (hasWater) {
      totalWater += waterUsed;
      totalWaterUsed += waterUsed * 20000;
    }
  }
});

setData({
  totalRoom: totalRoom.toLocaleString(),
  totalElectric: totalElectric > 0 ? totalElectric : undefined,
  totalWater: totalWater > 0 ? totalWater : undefined,
  totalElectricUsed: totalElectricUsed > 0 ? totalElectricUsed.toLocaleString() : undefined,
  totalWaterUsed: totalWaterUsed > 0 ? totalWaterUsed.toLocaleString() : undefined,
  roomCount: roomCount.length ? roomCount.join(', ') : 'Khng c phng no ph hp'
});

    
}, [props]); // nn thm dependency l props nu n c th thay i

  return (
   <div className={cx("container")}>
        <div className={cx("result")}>
            <div className={cx("result-item")}>Tng tin Phng: {data.totalRoom} ng</div>
            <div className={cx("result-item")}>Tng tin in: {data.totalElectricUsed} ng</div>
            <div className={cx("result-item")}>Tng tin Nc: {data.totalWaterUsed} ng</div>
            <div className={cx("result-item")}>Tng lng in s dng trong thng: {data.totalElectric} kWh</div>
            <div className={cx("result-item")}>Tng lng nc s dng trong thng: {data.totalWater} m3</div>
            <div className={cx("result-item")}>Cc phng  tnh: {data.roomCount}</div>
            <div className={cx("result-item")}><button className={cx("button")}>Quay li</button></div>

        </div>
   </div>
  );
}

export default TotalPaymentInfo;